.swiper {
    width: 100%;
    height: 100vh;
  }
  
  .swiper-slide {
    text-align: center;
    font-size: 18px;
    padding: 0 20px;
    background: #fff;
  
    /* Center slide text vertically */
    display: -webkit-box;
    display: -ms-flexbox;
    display: -webkit-flex;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    -webkit-justify-content: center;
    justify-content: center;
    -webkit-box-align: center;
    -ms-flex-align: center;
    -webkit-align-items: center;
    align-items: center;
  }
  
  .swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: contain;
  }
  
  .vertical_description{
    position: absolute;
    top: 60%;
    left: 20px;
    width: 400px;
    h4{
        font-size: 25px;
        margin-bottom: 10px;
    }
    p{
        opacity: 0.5;
    }
  }